import { Manager, ADD_TO_BAG, SIDEPANEL_OPEN, SIDEPANEL_CLOSE, WINDOW_DID_SCROLL, SIDEPANEL_QUICKVIEW_OPEN, VARIANT_UPDATED } from 'lib/events'
import { select, selectAll, closest, getCollection } from 'lib/selectors'
import Accordion from 'modules/accordion'
import tabbable from 'tabbable'
import Clipboard from 'clipboard'
import { setWishlistContainsProductsTitle, updateUI } from 'lib/wishlist/wishlist-ui'
import { getWishlistData } from 'lib/wishlist/wishlist-yapi'
import bind from 'delegate'
import HeaderScrollAnimation from 'ui/header/scroll-animation'
import { isWideScreen, tabIndexToZero,initKakao } from 'lib/helpers'
import { read as readCookie } from 'modules/cookie'
import notifier from 'modules/notifier'
import config from 'lib/config'
import { get } from 'modules/fetch'
   
const decode = require('urldecode')
let initialKakaoAlready = 0
/* eslint-disable */

/**
 * @param el
 * @constructor
 */
function Wishlist(el) {
    // Define template types
    const SIMPLE_AXIS = 'SIMPLE_AXIS'
    const MULTI_AXIS = 'MULTI_AXIS'
    const LOOKS_AXIS = 'LOOKS_AXIS'

    // Define products categories
    const PRODUCTS = 'PRODUCTS'
    const LOOKS = 'LOOKS'

    // Define counter type
    const INCREMENT = 1
    const DECREMENT = -1
    const EQUALIZE = 0

    // Define counter names
    const lookText = select('#wishlistLook') ? select('#wishlistLook').value : ''
    const looksText = select('#wishlistLooks') ? select('#wishlistLooks').value : ''
    const productsText = select('#wishlistProducts') ? select('#wishlistProducts').value : ''
    const productText = select('#wishlistProduct') ? select('#wishlistProduct').value : ''

    let scrollAnimation
    let isLastItemInWishlist = false
    // Define locals
    // Current selection
    let cardEl
    // Current container selection
    let cardCol
    //Current container panel selection
    let cardPanel
    // Current counter label
    let cardCounter
    // All items
    let cardEls
    // Current item section
    let sectionCol
    // Store simple DOM to allow undo feature
    let backupEl
    let backupAxisEl
    let backupTitleEl
    // Next focusable el after the notification is closed
    let nextFocusableEl

    // Get title ref
    const shareLinkEl = select('.share-link') ||false
    const clipboard =  new Clipboard('.share-wishlist__button')
    const wishlistProductClickCTA = selectAll('.js-wishlist-item p[data-plp-product-click]')

    function init() {
        initAccordion()
        initWishlistStars()
        initClipboard()
        initRole()
        if (window.config.country === "KR") {
          initKakao()
        }
        bind(document.body, '.js-notifier-close', 'click', focusMoveWishlist)
        bind(document.body, '[data-popup-target="quickview"][data-wishlist-active="true"]', 'click', productQuickviewCTA)
        
        const panelList = getCollection('.js-open-wishlist-accordion-cta')

        panelList.forEach((panel) => {
          panel.addEventListener('click', (e) => {

            let isCollasped = e.currentTarget.attributes["aria-expanded"].value
            let selectedAxis = e.currentTarget.attributes["data-axis"].value
            let wishListCTAObj = {
              eventCategory: 'wishlist',
              eventAction: (isCollasped === "true") ? 'expand' : 'close',
              eventLabel: selectedAxis,
              event: 'uaevent'
            }
            if (isCollasped === "true") {
              let GAData = Object.assign({}, dataLayerGA.wishListMultiplePageLoad);
              GAData.ecommerce = GAData.ecommerce[selectedAxis];
              dataLayer.push(GAData)
              dataLayer.push(wishListCTAObj);

            } else {
              dataLayer.push(wishListCTAObj);

            }
            });
        });

        wishlistProductClickCTA.forEach((productClick) => {
          productClick.addEventListener('click', (e) => {
            let productId = e.currentTarget.dataset.id
            let productElement = e.isTrusted ? e.target.dataset.productElement : "view details"
            let SelectedProduct = JSON.parse(JSON.stringify(dataLayerGA.productList[productId]))
            let clickCTAObj = {
              "ecommerce": {
                click: ''
              }
            }
            clickCTAObj.ecommerce.click = {
              actionField: {
                list: 'wishlist'
              },
              products: SelectedProduct.cartEntryProductData.ecommerce.impressions[0]
            }
            clickCTAObj.event = 'uaevent'
            clickCTAObj.eventCategory = 'product list'
            clickCTAObj.eventAction = 'wishlist'
            clickCTAObj.eventLabel = productElement
            dataLayer.push(clickCTAObj)
          })
        })
    }
    
    function productQuickviewCTA(e) {
      if (window.config.pageName === 'wishlist') {
        let product = e.delegateTarget.getAttribute('data-id') || false
        if (product) {
          let virtualPageViewCTA = JSON.parse(JSON.stringify(dataLayerGA.productList[product]))
          virtualPageViewCTA.cartEntryProductData.event = 'virtualpageview'
          virtualPageViewCTA.cartEntryProductData.ecommerce.detail = {
            actionField: {
              list: 'wishlist'
            },
            products: virtualPageViewCTA.cartEntryProductData.ecommerce.impressions
          }
          virtualPageViewCTA.cartEntryProductData.eventCategory = 'product list'
          virtualPageViewCTA.cartEntryProductData.eventAction = 'wishlist'
          virtualPageViewCTA.cartEntryProductData.eventLabel = 'add to cart'
          delete virtualPageViewCTA.cartEntryProductData.ecommerce.impressions
          dataLayer.push(virtualPageViewCTA.cartEntryProductData)
        }
      }
    }

    function headerAnimateReset() {
        const header = select('.header__outer')
        scrollAnimation = new HeaderScrollAnimation(header)
        scrollAnimation.reset()
    }

    /**
     * @returns {Clipboard}
     */
    function clipboardInstance() {
        if(clipboard instanceof Clipboard) {
            return clipboard
        }

        const shareLinkEl = select('.share-wishlist__button', document)
        const clipboard = new Clipboard(shareLinkEl)

        return clipboard
    }

    function initClipboard() {
        Manager.subscribe(SIDEPANEL_OPEN, () => {
            return clipboardInstance()
        })

        Manager.subscribe(SIDEPANEL_CLOSE, () => {
            clipboardInstance().destroy()
    })
    }

    function initAccordion() {
        // eslint-disable-next-line no-new
        new Accordion(el)
    }
      
    function initRole() {
        let itemsList = selectAll('[data-wishlist]', el);
        if(itemsList.length > 0) {
            select('.assign-role').setAttribute("role", "presentation");
        }
    }

    function initWishlistStars() {
        selectAll('[data-wishlist]', el).forEach(wishlistStarsEl => {
            wishlistStarsEl.addEventListener('WISHLIST_CHANGE', handleWishlistChange)
            wishlistStarsEl.addEventListener('WISHLIST_NOTIFICATION_CLOSE', handleWishlistNotificationClose)
        })

        Manager.subscribe(SIDEPANEL_QUICKVIEW_OPEN, (sideMenu) => {

          wishlistChangeEventListener(sideMenu.panel)
        })

        Manager.subscribe(VARIANT_UPDATED, (variantSideMenu) => {
          wishlistChangeEventListener(variantSideMenu)
        })


        Manager.subscribe(ADD_TO_BAG, handleAddToBag)
    }

    const wishlistChangeEventListener = (el) => {
      const sideMenuStar = select('[data-wishlist]', el)
      if (sideMenuStar) {
        sideMenuStar.addEventListener('WISHLIST_CHANGE', () => {
          handleWishlistChange(event, el, true)
        })
        sideMenuStar.addEventListener('WISHLIST_NOTIFICATION_CLOSE', handleWishlistNotificationClose)
      }
    }

    function handleWishlistNotificationClose() {
        if (nextFocusableEl) {
            nextFocusableEl.focus()
            nextFocusableEl = undefined
        }
    }

    /**
     * @param sku
     */
    function handleAddToBag({sku}) {
        const wishlistButtonEls = selectAll(`[data-wishlist="${sku}"]`)

        wishlistButtonEls.forEach(wishlistButtonEl => {
            const cardElFromBag = closest(wishlistButtonEl, '.js-wishlist-item')

            if (cardElFromBag) {
                getWishlistData().then(data => {
                    removeWishlistItem(cardElFromBag, true)
                    updateUI(data, true)
                })
            }
        })
    }

    /**
     * @TODO: Refacto
     */
    function updateShareWishlistLink() {
        const wishlistLinkEl = select('.wishlist') ||false
        const disclaimerEl = select('.js-disclaimer') ||false
        const container = select('.js-main-content.wishlist .container') || false
        let counter = getCollection('.js-wishlist-item:not(.is-hidden)').length
        //hide the wishlist
        if (shareLinkEl) {
            shareLinkEl.classList.toggle('is-hidden', (counter < 1))
        }

        if (wishlistLinkEl) {
            wishlistLinkEl.classList.toggle('already-has-product', (counter < 1))
        }

        const disclaimers = selectAll('.js-disclamed') || false
        let disclaimerCounter = 0
        if (disclaimers) {
            for (let k = 0; k < disclaimers.length; k++) {
                let parentCol = closest(disclaimers[k], '.collapse-panel')
                if(parentCol){
                    if (!disclaimers[k].classList.contains('is-hidden')
                        && !parentCol.classList.contains('is-hidden')) {
                        disclaimerCounter++
                    }
                }
            }
        }

        if (disclaimerEl) {
            disclaimerEl.classList.toggle('is-hidden', (counter < 1))
        }
    }

    /**
     * @param event
     * @param isSidePanel (boolean to check if side panel is open
     * @param panel (side panel HTML element)
     */
    function handleWishlistChange(event, panel = null,  isSidePanel = false) {
      const eventTarget = select(`[data-wishlist="${event.currentTarget.getAttribute('data-wishlist')}"]`)
        if (isSidePanel && eventTarget) {
            if (event.detail.isVisible) {
               return removeWishlistItem(eventTarget)
            } else {
              const panelProduct = closest(event.detail.el, 'li')
              if (panelProduct) {
                return undoWishlistItem(eventTarget)
              } else {
                return createProductTemplate(panel, eventTarget)
              }
            }

        } else {
          if (event.detail.isVisible) {
            return removeWishlistItem(event.currentTarget)
          }
          return undoWishlistItem(event.currentTarget)
        }
    }

    const createProductTemplate = (panel, eventTarget) => {
      const productPanelSelector = eventTarget.getAttribute('data-axis-type') // get axis type
      const productPanel = select(`[data-axis=${productPanelSelector}] > li:first-child`) // get first element of list to copy the HTML template
      let oldProductSku
      if (productPanel) oldProductSku = select('[data-wishlist]', productPanel).getAttribute('data-wishlist') // get the old sku to replace in the copied template
      get(window.config.webservices.product.replace('{0}', `${event.currentTarget.getAttribute('data-wishlist')}?options=basic,variants&site=chanel`)) // get new variant data
        .then((data) => {
          if (productPanel && panel && oldProductSku) {
            const newProduct = document.createElement('li') //create new element to append to the list
            let regexToReplace = new RegExp(oldProductSku, 'g') // dynamic sku regex
            let newProductHtml = productPanel.innerHTML // copy element template to the new created li
            newProductHtml = newProductHtml.replace(regexToReplace, data.code) // replace sku in all occurrences in new product
            newProduct.innerHTML = newProductHtml //
            const panelImage = select('.product-details__packshot > img', panel)
            const newProductPrice = select('.is-price', newProduct)
            const newProductImage = select('.product__media', newProduct)
            const newProductShadeName = select('.is-starting-from > p:first-child', newProduct)
            const panelProductShadeName = select('[data-category=Shade] > .button__wrapper > span', panel)
            const newProductLink = select('a', newProduct)
            if (newProductImage && panelImage) { // replace image in new created element
              newProductImage.innerHTML = ""
              newProductImage.appendChild(panelImage)
            }
            if (newProductPrice) newProductPrice.innerHTML = data.price // replace price
            if (newProductShadeName && panelProductShadeName) newProductShadeName.innerHTML = panelProductShadeName.innerHTML // replace shade name
            if (newProductLink) newProductLink.setAttribute('href', `${window.config.baseUrl}/p/${data.code}/`) //replace a href link to redirect to the new product PDP
            const newProductStarBtn = select('[data-wishlist]', newProduct)
            if (newProductStarBtn) newProductStarBtn.addEventListener('WISHLIST_CHANGE', () => {
              handleWishlistChange(event, null, true)
            })
            newProduct.classList.add('product-grid__item', 'js-wishlist-item')
            productPanel.parentNode.insertBefore(newProduct, productPanel.parentNode.childNodes[0]) // prepend the new element to the wishlist page content
          }
        })
    }

    function toggleRoleAttr() {
        let hiddenCardEls = 0;
        for(let i = 0; i < cardEls.length; i++) {
            if(cardEls[i].classList.contains('is-hidden')) { 
                hiddenCardEls = hiddenCardEls + 1; 
            }
        }
        return hiddenCardEls;
    }

    /**
     * @param targetEl
     */
    function removeWishlistItem(targetEl, fromBag) {
        let isFromAddToBag = fromBag || false

        cardEls = selectAll('.js-wishlist-item', el) // Get all items
        cardEl = (isFromAddToBag) ? targetEl : closest(targetEl, '.js-wishlist-item') // Set item card
        cardCol = closest(targetEl, '.js-wishlist-grid') // Set current items container
        cardPanel = closest(targetEl, '.collapse-panel') // Set current items container panel
        nextFocusableEl = getNextFocusableEl(cardEls, cardEl, el) // Get next available focusable element
        cardCounter = select('.collapse-panel.is-active .collapse-panel__label', el) // Set counter label element
        sectionCol = closest(targetEl, 'section') // Set current items section
        
        const hiddenPanels = getCollection('div.collapse-panel.is-hidden', select('.js-main-content'))
        if (hiddenPanels) {
            hiddenPanels.forEach((panel) => {
                panel.parentElement.removeChild(panel)
            })
        }

        
        removeProductCard()

        let hiddenCardElsCount = toggleRoleAttr();
        if(cardEls.length == hiddenCardElsCount) {
            select('.assign-role').removeAttribute("role");
        }

        updateCounters(DECREMENT)
        updateShareWishlistLink()
        
        const wishListCollapsePanels = getCollection('div.collapse-panel', select('.js-main-content'))
      if (wishListCollapsePanels.length === 1) {
        TweenLite.set(wishListCollapsePanels[0].children[0], { display: 'none' })
        TweenLite.set(wishListCollapsePanels[0].children[1], { display: 'block' })
      } else if (wishListCollapsePanels.length > 1) {
        wishListCollapsePanels.forEach((collapsePanel) => {
            if (collapsePanel.classList.contains('is-active')) {
              const items = getCollection('.js-wishlist-item:not(.is-hidden):not(.look-grid__item)', collapsePanel)
              const hasLookItems = getCollection('.js-look-panel:not(.is-hidden)', collapsePanel)
              if (items.length === 0 && hasLookItems.length === 0) {
                TweenLite.set(collapsePanel.children[0], { display: 'none' })
                TweenLite.set(collapsePanel.children[1], { display: 'none' })
                collapsePanel.classList.toggle('is-hidden')
                const DisplayedPanel = getCollection('div.collapse-panel:not(.is-hidden)', select('.js-main-content'))
                if (DisplayedPanel.length === 1 ) {
                  TweenLite.set(DisplayedPanel[0].children[0], { display: 'none' })
                  TweenLite.set(DisplayedPanel[0].children[1], { display: 'block' })
                }
              }
              else {
              	const DisplayedPanel = getCollection('div.collapse-panel:not(.is-hidden)', select('.js-main-content'))
                  if (DisplayedPanel.length === 1 ) {
                    TweenLite.set(DisplayedPanel[0].children[0], { display: 'none' })
                    TweenLite.set(DisplayedPanel[0].children[1], { display: 'block' })
                  }
               }
            }
        })


      }
    }
    let collapsePanelHeader
    let collapsePanelContent
    /**
     * @param event
     */
    function undoWishlistItem(event) {
      let hiddenCardElsCount = toggleRoleAttr();
        
      undoProductCard()
      if(cardEls.length == hiddenCardElsCount) {
        select('.assign-role').setAttribute("role", "presentation");
      }
        
      updateCounters(INCREMENT)
      updateShareWishlistLink()
      setWishlistContainsProductsTitle()
      const activePanel = select('div.collapse-panel.is-active')
      if (activePanel) {
        activePanel.classList.remove('is-hidden')
        TweenLite.set(activePanel.children[0], {display: 'block'})
        TweenLite.set(activePanel.children[1], {display: 'block'})
      }

      let isLastSectionItem = isLastListItem()
      if(sectionCol && isLastSectionItem) {
        let looksHeader = select('.js-look-panel')
        let hasLooksHiddenPanel =  getCollection('.js-look-panel:not(.is-hidden)')
        sectionCol.classList.contains('looks') ? looksHeader && hasLooksHiddenPanel.length > 0 ?  sectionCol.removeAttribute('hidden', '') : '' : sectionCol.removeAttribute('hidden', '')
      }
      
      const multipleAxisPanels = selectAll('div.collapse-panel')
      if (multipleAxisPanels && multipleAxisPanels.length > 1) {
        for (let i = 0; i < multipleAxisPanels.length; i++) {
          if (!multipleAxisPanels[i].classList.contains('is-active')) {
            collapsePanelHeader = multipleAxisPanels[i].children[0]
            collapsePanelContent = multipleAxisPanels[i].children[1]
            TweenLite.set(collapsePanelHeader, {display: 'block'})
            TweenLite.set(collapsePanelContent, {display: 'none'})
          }
        }
      }
      // TweenLite.set(collapsePanelHeader, { display: 'block' })
      Manager.subscribe('onNotifierIn', movingFocusToNotification)
      const disclaimerEl = select('.js-disclaimer')
      const disclamedItems = selectAll('.js-disclamed')
      if (disclaimerEl && disclaimerEl.classList.contains('is-hidden') && disclamedItems.length > 0) {
        disclaimerEl.classList.remove('is-hidden')
      }
    }
    function movingFocusToNotification(){
        let notifierText = select('.notifier__block p')
        if(notifierText){
            notifierText.focus()
        }
    }
    /**
     * @returns {number}
     */
    function countAxisProductsItems() {
        let counter = 0
        let productsList = getCollection('.product-grid', cardPanel) || false

        productsList.forEach((item) => {
            if (item
                && item.children
                && !item.parentElement.classList.contains('is-hidden')) {
                for (let i=0; i < item.children.length; i++) {
                    counter += (!item.children[i].classList.contains('is-hidden')) ? INCREMENT : EQUALIZE
                }
             }
        })

        return counter
    }

    /**
     * @returns {number}
     */
    function countAxisLooksItems() {
        let counter = 0
        let looksList = select('.looks', cardPanel) || false

        if (looksList && looksList.children) {
            for (let i=0; i < looksList.children.length; i++) {
                if (!looksList.children[i].classList.contains('is-hidden')) {
                    let listItems = looksList.children[i].children[1]
                    if(listItems) {
                      for (let j=0; j < listItems.children.length; j++) {
                        counter += (!listItems.children[j].classList.contains('is-hidden')) ? INCREMENT : EQUALIZE
                      }
                   }
                }
            }
        }

        return counter
    }

    /**
     * @param counterType
     * @returns {number}
     */
    function countMultiAxisItems(counterType) {
        switch (counterType) {
            case PRODUCTS:
                return countAxisProductsItems()
            case LOOKS:
                return countAxisLooksItems()
            default:
            // NONE
        }
    }

    /**
     * @param direction
     */
    function updateSimpleAxisCounter(direction) {
        const counterValue =  countAxisProductsItems()
        const counterText = (counterValue > 1)
            ? `${counterValue}  ${productsText}` : `${counterValue}  ${productText}`

        if (cardCounter) {
            cardCounter.innerHTML = counterText
        }
    }

    /**
     * @param direction
     */
    function updateMultiAxisCounter(direction) {
        let counterProductsValue = countMultiAxisItems(PRODUCTS)
        let counterLooksValue =  countMultiAxisItems(LOOKS)

        const counterProductText = (counterProductsValue > 1)
            ? `${counterProductsValue}  ${productsText}` : `${counterProductsValue}  ${productText}`

        const counterLookText = (counterLooksValue > 1)
            ? `${counterLooksValue}  ${looksText}` : `${counterLooksValue}  ${lookText}`

        let counterText = '-'
        if (counterProductsValue > 0 && counterLooksValue > 0) {
            counterText = counterProductText + ', ' + counterLookText
        }
        else if (counterProductsValue > 0 && counterLooksValue < 1 ) {
            counterText = counterProductText
        }
        else if (counterProductsValue < 1 && counterLooksValue > 0 ) {
            counterText = counterLookText
        }

        if (cardCounter) {
            cardCounter.innerHTML = counterText
        }
    }

    /**
     * @param direction
     */
    function updateTotalCounter(direction) {}

    /**
     * @param direction
     */
    function updateCounters(direction) {
        switch (getWishlistTemplateType()) {
            case MULTI_AXIS:
                updateSimpleAxisCounter(direction)
                break
            case LOOKS_AXIS:
                updateMultiAxisCounter(direction)
                break
            default:
            // NONE
        }
        
        updateTotalCounter(direction)
    }

    function removeItemFromSimpleWishlist() {
        let isLastItem = isLastListItem()
        isLastItemInWishlist = isLastItem
        backupEl = isLastItem ? backupEl = closest(cardEl, '.js-look-panel') || cardEl : cardEl
        backupEl ? backupEl.classList.toggle('is-hidden') : config.noop()
        if (isLastItem && closest(cardEl, '.js-look-panel') === null && backupEl && backupEl.parentElement.previousElementSibling != null) backupEl.parentElement.previousElementSibling.classList.toggle('is-hidden')
        let looksHeader = select('.js-look-panel')
        let hasLooksPanel =  getCollection('.js-look-panel:not(.is-hidden)')
        if (isLastItem && hasLooksPanel.length === 0 && closest(cardEl, '.js-look-panel') !== null && looksHeader && looksHeader.previousElementSibling !== null) looksHeader.previousElementSibling.classList.toggle('is-hidden')
        if (isLastItem) {
            if(sectionCol) {
                let hasLooksActivePanel =  getCollection('.js-look-panel')
                let hasLooksHiddenPanel =  getCollection('.js-look-panel.is-hidden')
                sectionCol.classList.contains('looks') ? looksHeader && hasLooksActivePanel.length == hasLooksHiddenPanel.length ?  sectionCol.setAttribute('hidden', '') : '' : sectionCol.setAttribute('hidden', '')
            }

            const footer = select('footer')
            if (footer) {
                footer.classList.remove('has-animation')
                footer.style.minHeight = "auto"
                footer.style.height = "auto"
            }
            select('.subfooter__container').style.opacity = "1"
            Manager.publish('WINDOW_DID_SCROLL')
            headerAnimateReset()
        }
    }

    function removeItemFromSimpleAxis() {
        removeItemFromSimpleWishlist()

        let isLastAxis = isLastListAxis()
        if (!isLastAxis) backupAxisEl = getNextAxis()
        else {
            backupAxisEl = getNextAxis()
            if (backupAxisEl && backupAxisEl.header && backupAxisEl.content) {
                backupAxisEl.header.classList.toggle('is-hidden')
                TweenLite.set(backupAxisEl.content, { display: 'block' })
            }
        }
    }

    /**
     * @returns {*}
     */
    function removeItemFromMultiAxis() {
        if (!isLastListItem()) {
            return removeItemFromSimpleWishlist()
        }

        if (isLastAxisGrid()) {
            return removeItemFromSimpleAxis()
        }

        return removeItemFromSimpleWishlist()
    }

    function removeProductCard() {
        switch (getWishlistTemplateType()) {
            case SIMPLE_AXIS:
                removeItemFromSimpleWishlist()
                break
            case MULTI_AXIS:
              removeItemFromSimpleWishlist()
                break
            case LOOKS_AXIS:
                removeItemFromMultiAxis()
                break
            default:
            // NONE
        }
    }

    function undoItemFromSimpleWishlist() {
        if (isLastItemInWishlist && closest(cardEl, '.js-look-panel') === null && backupEl && backupEl.parentElement.previousElementSibling != null) backupEl.parentElement.previousElementSibling.classList.toggle('is-hidden')
        let looksHeader = select('.js-look-panel')
        let hasLooksPanel =  getCollection('.js-look-panel:not(.is-hidden)')
        if (isLastItemInWishlist && hasLooksPanel.length === 0 && closest(cardEl, '.js-look-panel') !== null && looksHeader && looksHeader.previousElementSibling !== null) looksHeader.previousElementSibling.classList.toggle('is-hidden')
        if (backupEl) {
            const footer = select('footer')
            backupEl.classList.toggle('is-hidden')
            if (footer) {
                footer.classList.add('has-animation')
                footer.style.minHeight = "100vh"
                footer.style.height = "100%"
            }
            Manager.publish('WINDOW_DID_SCROLL')

        }

        backupEl = null
    }

    function undoItemFromSimpleAxis() {
        undoItemFromSimpleWishlist()

        if (backupTitleEl && backupTitleEl.header && backupTitleEl.content) {
            backupTitleEl.header.classList.toggle('is-hidden')
            TweenLite.set(backupTitleEl.content, {display: 'none'})
        }

        if (backupAxisEl && backupAxisEl.header && backupAxisEl.content) {
            if (isLastListAxis()) {
                TweenLite.set(backupAxisEl.header, { display: 'none' })
                TweenLite.set(backupAxisEl.content, { display: 'block' })
            } else {
                TweenLite.set(backupAxisEl.content, { display: 'none' })
            }
        }

        backupAxisEl = null
        backupTitleEl = null
    }

    function undoItemFromMultiAxis() {
        undoItemFromSimpleAxis()
    }

    function undoProductCard() {
        switch (getWishlistTemplateType()) {
            case SIMPLE_AXIS:
                undoItemFromSimpleWishlist()
                break
            case MULTI_AXIS:
                undoItemFromSimpleAxis()
                break
            case LOOKS_AXIS:
                undoItemFromMultiAxis()
                break
            default:
            // NONE
        }
    }

    /**
     * @returns {string}
     */
    function getWishlistTemplateType() {
        return (cardPanel)
            ? ((select('.looks', cardPanel)) ? LOOKS_AXIS : MULTI_AXIS)
            : SIMPLE_AXIS
    }

    function cleanBackup() {
        if (backupEl) {
            backupEl.remove()
        }
    }

    /**
     * @returns {boolean}
     */
    function isLastListItem() {
        const cardColItems = getCollection('.js-wishlist-item:not(.is-hidden)', cardCol)
        return cardColItems.length === 1

    }

    /**
     * @returns {boolean}
     */
    function isLastListAxis() {
        let container = select('.js-main-content.wishlist .container') || false
        let list = selectAll('.collapse-panel', container)
        if (list.length === 1) {
            return  true
        }

        if (list.length < 2) {
            for (let i = 0; i < list.length; i++) {
                if (!list[i].classList.contains('is-hidden')) {
                    return true
                }
            }
        }

        return false
    }

    /**
     * @returns {boolean}
     */
    function isLastAxisGrid() {
        let productList = select('.product-grid', cardPanel) || false
        let looksList = select('.looks', cardPanel) || false
        let rtwList = select('.rtw', cardPanel) || false
        let numberOfGrids = 0

        numberOfGrids = (
            productList
            && !productList.parentElement.classList.contains('is-hidden')
            && (productList.children.length > 0))
            ?(numberOfGrids + 1) : numberOfGrids

        if (looksList && looksList.children) {
            numberOfGrids = getNumberOfGrids(looksList, numberOfGrids)
        }
        if (rtwList && rtwList.children) {
            numberOfGrids = getNumberOfGrids(rtwList, numberOfGrids)
        }

        return (numberOfGrids < 2)
    }

    function getNumberOfGrids(list, gridNumber) {
        if (list && list.children) {
            for (let i=0; i < list.children.length; i++) {
                return gridNumber = (!list.children[i].classList.contains('is-hidden')) ?
                    (gridNumber + 1) : gridNumber
            }
        }
    }

    /**
     * @returns {*}
     */
    function getNextAxis() {
        let nextAxis
        let container = select('.container.is-accordion')
        let lists = selectAll('.collapse-panel', container)

        for (let i = 0; i < lists.length; i++) {
            if (!lists[i].classList.contains('is-hidden')
                && !lists[i].classList.contains('is-active')) {
                nextAxis = lists[i]
                break
            }
        }

        if (!nextAxis) {
            return  null
        }

        return {
            header: select('.collapse-panel__header', nextAxis),
            content: select('.collapse-panel__content', nextAxis)
        }
    }

    init()   
}

/**
 * @param cardEls
 * @param removedCardEl
 * @param fallbackEl
 * @returns {*}
 */
function getNextFocusableEl(cardEls, removedCardEl, fallbackEl) {
    const removedCardIndex = cardEls.indexOf(removedCardEl)
    let focusables

    if (cardEls.length > 1) {
        const focusIndex = removedCardIndex === 0 ? removedCardIndex + 1 : removedCardIndex - 1
        focusables = tabbable(cardEls[focusIndex])
    } else {
        focusables = tabbable(fallbackEl)
    }

    if (focusables.length) {
        return focusables[0]
    }

    return null
}

function manageAriaHiddenOnNotificationClose() {
    let multipleAxisPanels = selectAll('div.collapse-panel:not(.is-hidden)');
      if (multipleAxisPanels && multipleAxisPanels.length < 2) {
          for (let i = 0; i < multipleAxisPanels.length; i++) {
              if (!multipleAxisPanels[i].classList.contains('is-hidden')) {
                multipleAxisPanels[i].children[1].removeAttribute('aria-hidden');
              }
          }
      }
  }
/*Moving focus after popup or sidepanel closed*/
function focusMoveWishlist() {
  notifier.closeCurrentMessage()
  const lastUndoItem = select('.last-undo')
  const header = select('h1.heading')
  const cartHeader = select('div.cart h1')
  const shareLinkEl = select('p.share-link')
  manageAriaHiddenOnNotificationClose()
  if(lastUndoItem){
	lastUndoItem.lastElementChild.focus()
	lastUndoItem.removeAttribute('tabindex')
  } else if(header && (!addedToBagElement || shareLinkEl && shareLinkEl.classList.contains('is-hidden')) ) {
	header.setAttribute('tabindex', '-1')
	header.focus()
  } else if(addedToBagElement){
      addedToBagElement.setAttribute('tabindex', '-1')
      addedToBagElement.focus()
  } else if(cartHeader){
      cartHeader.setAttribute('tabindex', '-1')
      cartHeader.focus()
  }
  if(!isWideScreen()) {
   const links = selectAll('.collapse-panel__header .js-collapse-button')
   if (links) {
    tabIndexToZero(links)
   }
  }
}

document.body.addEventListener("touchstart", () => {
    if (notifier.modal) {
        setTimeout(() => {
            notifier.close()
        }, 100)
    }
});

window.addEventListener('scroll', () => {
  if (notifier.modal) {
    notifier.close()
  }
})

let addedToBagElement
let lastRemovedWishlist
document.querySelector('body').addEventListener('click', function(event) {
    if(event.target.classList.contains('is-tertiary') || event.target.parentElement.classList.contains('is-tertiary')){
        addedToBagElement = event.target
    }
    const wishlistPageTest = document.title
    const liveConnectClose = event.target.closest('button')
    if (event.target.classList.contains('is-close')
        || event.target.classList.contains('js-sidepanel-close')
        || event.target.classList.contains('js-notifier-close')) {
            if(liveConnectClose && liveConnectClose.classList.contains('js-close-button')){
                return false
            }
        focusMoveWishlist()
    }
    
    if (wishlistPageTest != 'Wishlist') {
        if (event.target.classList.contains('is-active')
            || event.target.classList.contains('is-wishlist')) {
            lastRemovedWishlist = event.target
        }
        if (event.target.classList.contains('undo') && lastRemovedWishlist
            || event.target.classList.contains('is-close')
            || event.target.classList.contains('js-sidepanel-close')
            || event.target.classList.contains('js-notifier-close')) {
                const kakaomore = select('.js-kakaotalk') 
                if (kakaomore) {
                  kakaomore.classList.remove('js-kakaotalk')	
                }
                if(lastRemovedWishlist)
                lastRemovedWishlist.focus()
        }
    }

    if (event.target.classList.contains('cart') || event.target.parentElement.classList.contains('cart')) {
    	 const cartDataJson = getCartDataFromCookie()
    	 const cartEntriesCount = cartDataJson ? cartDataJson.cartItemCount : 0
    	 const targetElement = event.target.classList.contains('cart') ? event.target : event.target.parentElement;
    	 const cartBtnElm = select('.cart.button.nav-item')
    	 const cartDic = window.config.locales.cart
    	 if (targetElement.classList.contains('already-has-product') && parseInt(cartEntriesCount) === 0) {
    		 targetElement.classList.remove('already-has-product')
    		 if (cartBtnElm) {
    		   cartBtnElm.setAttribute('title', cartDic.title)
            }
        }
    }
    const np = event.target.parentNode
    let dataTrack = {
    		eventCategory: 'wishlist', 
		    eventLabel: '',
		    event: 'uaevent'
        }
    
    if (np && np.id.toLowerCase()  === 'registerfromwishlist') {
        dataTrack.eventAction = 'create an account'
        window.document.DLKit.push('on-tracking', dataTrack)    
    }
    
    if (np && np.id.toLowerCase()  === 'loginfromwishlist') {
        dataTrack.eventAction = 'sign in'
        window.document.DLKit.push('on-tracking', dataTrack)    
    }
    if (event.target.parentElement.classList.contains('is-kakao')) {
      const kakaomore = select('.js-kakaotalk')
      if (kakaomore) {
        kakaomore.addEventListener('click', shareKakaotalk())
      }
    }
});

function getCartDataFromCookie() {
    try {
        const cookieValue = readCookie('active-cartStatus')
        if (cookieValue) {
            let cartDataJson = cookieValue
            if (cookieValue.indexOf("{") != 0) {
                cartDataJson = window.atob(decode(cookieValue))
            }
            return JSON.parse(cartDataJson)
        }
    } catch(e) {
        console.error(e)
        return null
    }

    return null
}

function looksPrintPrepare() {
	const images = selectAll('main.main .container img.is-lazy'),
    imagesSrcs = [];
    for (let i=0; i < images.length; i++){
                let srcset = images[i].getAttribute('data-srcset')
                if(srcset && !(srcset.split(' ')[0]).endsWith('null')) {
                  imagesSrcs[i] = srcset
                }
    }

    let promises = imagesSrcs.map((src,index) => new Promise((resolve, reject) => {
          let img = new Image();
          if(src && !(src.split(' ')[0]).endsWith('null')) {
               images[index].setAttribute('src',src.split(' ')[0])
          }
          images[index].setAttribute('srcset',src)
          images[index].classList.add("lazyautosizes","lazyloaded")
          resolve()          
      })
    )
    setTimeout(function(){Promise.all(promises).then(print)},500);
    
}

function printWishlist(){
    const printDisclamer = select('#print-media')
    if(printDisclamer){
        printDisclamer.removeAttribute('hidden')
    }
    looksPrintPrepare()
    
    dataLayer.push({
      eventCategory: 'wishlist',
      eventAction: 'print',
      eventLabel: '',
      event: 'uaevent'
    })
}
function spacebarAction(e) {
    if (document.activeElement != null && document.activeElement != undefined) {
        if(e.keyCode == 32 && (document.activeElement.classList.contains('print-wishlist'))){
            e.preventDefault()
            printWishlist()
        }
    }
}

function shareKakaotalk() {
  if (initialKakaoAlready == 0) {
	Kakao.init(window.config.webservices.kakao.key) 
	initialKakaoAlready++
  }
  const kakaoshare = document.querySelector('.js-kakaotalk')
  Kakao.Link.sendDefault({
    objectType: "feed",
    content : {
	  title: config.kakaoWishList.title, 
	  description: config.kakaoWishList.description,
	  imageUrl: config.webservices.kakao.imageUrl,
	  link : {
	    mobileWebUrl: kakaoshare.dataset.shareUrl? kakaoshare.dataset.shareUrl : "",
		webUrl: kakaoshare.dataset.shareUrl? kakaoshare.dataset.shareUrl : "" 
	  }
	},
	social : {
	  likeCount:0,    
	  commentCount:0, 
	  sharedCount:0  
	},
	buttons : [
	  {
		title: config.kakaoWishList.button, 
		link : {
		  mobileWebUrl: kakaoshare.dataset.shareUrl? kakaoshare.dataset.shareUrl : "",
		  webUrl: kakaoshare.dataset.shareUrl? kakaoshare.dataset.shareUrl : ""  
		}
	  }
	]
  })
}

const printSelector = '.print-wishlist'
bind(document.body, printSelector, 'click', printWishlist)
document.body.onkeydown = function(e){
    spacebarAction(e)
}
